.materials {
  margin-top: -60px;
  @include flex(center, normal, row, wrap);
  margin-left: -10px;
  margin-right: -10px;
  @include sm {
    margin-top: 0; }
  &__item {
    width: 20%;
    @include sm {
      width: 33.33333%;
      margin-bottom: 10px; }
    @include xs {
      width: 50%; } } }

.material-link {
  $this: &;
  @include flex(flex-start, center, column);
  text-align: center;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  @include hover {
    #{$this}__title {
      color: $accent; } }
  &__img {
    @include flex(center, center);
    flex-grow: 1;
    // padding-top: 60%
    margin-bottom: 10px;
    // background-repeat: no-repeat
    // background-position: 50% 50%
 }    // background-size: contain
  &__title {
    @extend .title;
    @extend .title--medium;
    @extend .title--white;
    @extend .title--h5;
    @extend .title--ttu;
    @include tr(color .3s); } }
